<template>
  <div class="hello">
    <button @click="getPhoneInfo()">点击获取手机设备的信息</button>
    <p v-html="phoneInfo"></p>
    <button @click="getJson()" style="margin-top: 60px">点击调用原生方法，获取一串json</button>
    <p v-html="json"></p>
  </div>
</template>


<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      phoneInfo: '',
      json: ''
    }
  },
  methods: {
    getPhoneInfo() {
      try {
        let phoneInfo = window.phone.getPhoneInfo()
        this.phoneInfo = phoneInfo
      } catch (e) {}
    },
    getJson() {
      try {
        let json = JSON.parse(window.phone.getJson())
        this.json = json
      } catch (e) {}
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .hello {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .hello button {
    height: 40px;
    line-height: 40px;
    background-color: #67C23A;
    color: white;
    font-size: 16px;
    border-radius: 20px;
    border-color: transparent;
    padding: 0 20px;
    outline: none;
  }
  .hello p {
    width: 100%;
  }
</style>



